import { css, styled } from 'styled-components';

import * as device from '#app/constants/devices';

import { isPdf } from '#app/util/edition';

const Wrapper = styled.a`
  color: ${(props) => props.theme.color.editionPrimary};
  border-top: 1px solid ${(props) => props.theme.color.editionPrimary};
  width: 100%;

  &:hover {
    color: ${(props) => props.theme.color.editionPrimary};
  }
`;

const ContentBody = styled.div`
  column-count: 1;
  font-family: ${(props) => props.theme.fontFamily.editionRegularFont};
  font-weight: 300;
  column-gap: 16px;
  em {
    font-family: ${(props) => props.theme.fontFamily.editionLightItalicFont};
  }
  @media ${isPdf} {
    column-count: 3;
    column-fill: auto;
    break-word: break-all;
    overflow-wrap: anywhere;
    &.col-fill {
      height: 180px;
      column-fill: auto;
    }

    &.col-balance {
      column-fill: balance;
    }
  }
`;

const Markup = styled.div`
  font-size: 14px;
  line-height: 15px;
  font-weight: 400;
  @media ${device.maxPdf} {
    margin: 10px 0;
  }
  p,
  div {
    text-align: left;
    margin: 0;
    font-family: ${(props) => props.theme.fontFamily.editionRegularFont};
    font-size: inherit;
    line-height: 16px; /* fixed to this value, so the text column evenly distributed */
  }
`;
const Author = styled.div`
  font-family: ${(props) => props.theme.fontFamily.editionSansBoldFont};
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  padding: 2px 0;
  margin-bottom: 10px;
  border-top: 1px solid ${(props) => props.theme.color.editionPrimary};
  border-bottom: 1px solid ${(props) => props.theme.color.editionPrimary};
  column-span: 1;

  .author-info {
    font-weight: normal;
    font-family: ${(props) => props.theme.fontFamily.editionSansLightFont};
    display: block;
  }

  @media ${device.tideLaptop} {
    font-size: 14px;
  }
`;
const Capsule = styled.div`
  text-transform: uppercase;
  font-family: ${(props) => props.theme.fontFamily.editionSansBoldFont};
  color: ${(props) => props.theme.color.primary.base};
  line-height: 16px;
`;
const HeadlineWrapper = styled.div`
  font-size: 52px;
  line-height: 48px;
`;
export const Headline = styled.h1`
  text-align: center;
  font-family: ${(props) => props.theme.fontFamily.editionSerifBoldFont};
  margin: ${(props) => props.theme.spacing.x1_5}px 0
    ${(props) => props.theme.spacing.x0_25}px;
  font-size: 40px;
  line-height: 40px;
  font-weight: bold;
  letter-spacing: -0.0162em;

  &.size-small {
    font-size: 35px;
    line-height: 35px;
    letter-spacing: -0.0185em;
  }

  &.size-large {
    font-size: 45px;
    line-height: 45px;
    letter-spacing: -0.0144em;
  }

  @media ${isPdf} {
    font-size: 70px;
    line-height: 70px;
    letter-spacing: -0.0092em;

    &.size-small {
      font-size: 60px;
      line-height: 60px;
      letter-spacing: -0.0108em;
    }

    &.size-large {
      font-size: 80px;
      line-height: 80px;
      letter-spacing: -0.0081em;
    }
  }

  .sport & {
    font-size: 30px;
    line-height: 32px;
    font-weight: 500;
    margin: 16px 0 14px;
    letter-spacing: 0.3px;
    @media ${isPdf} {
      font-size: 0.8em;
      line-height: 40px;
      margin: 12px 0 20px;
    }

    &.article-2 {
      margin-bottom: 10px;
    }
  }
`;

const SubHeadline = styled.div`
  text-align: center;
  font-family: ${(props) => props.theme.fontFamily.editionMediumFont};
  font-weight: 500;
  margin-bottom: ${(props) => props.theme.spacing.x1_5}px;

  p,
  li {
    font-size: 18px;
    margin: 0;
    line-height: 20px;
    letter-spacing: -0.0277em;

    li::before {
      width: 14px;
      height: 14px;
    }
  }

  &.size-small {
    p,
    li {
      font-size: 16px;
      line-height: 18px;
      letter-spacing: -0.0312em;
    }

    li::before {
      width: 12px;
      height: 12px;
    }
  }

  &.size-large {
    p,
    li {
      font-size: 20px;
      line-height: 22px;
      letter-spacing: -0.025em;
    }

    li::before {
      width: 16px;
      height: 16px;
    }
  }

  @media ${isPdf} {
    p,
    li {
      font-size: 20px;
      line-height: 22px;
      letter-spacing: -0.025em;

      li::before {
        width: 16px;
        height: 16px;
      }
    }

    &.size-small {
      p,
      li {
        font-size: 18px;
        line-height: 20px;
        letter-spacing: -0.0277em;
      }

      li::before {
        width: 14px;
        height: 14px;
      }
    }

    &.size-large {
      p,
      li {
        font-size: 22px;
        line-height: 24px;
        letter-spacing: -0.0227em;
      }

      li::before {
        width: 18px;
        height: 18px;
      }
    }
  }

  ul {
    list-style: none;
    text-align: left;
    margin: 0;
    padding-left: 0;

    li {
      &:not(:last-child) {
        margin-bottom: ${(props) => props.theme.spacing.x0_5}px;
      }

      &:before {
        content: '';
        position: relative;
        background-color: ${(props) => props.theme.color.primary.base};
        display: inline-block;
        width: 16px;
        height: 16px;
        border-radius: 100%;
        margin-right: 5px;
        margin-top: 0;
        top: 2px;
      }
    }
  }
`;

const WobTitle = styled.h3`
  background-color: ${(props) => props.theme.color.primary.base};
  padding: ${(props) => props.theme.spacing.x0_5}px
    ${(props) => props.theme.spacing.x1}px;
  color: ${(props) => props.theme.color.ink.inverted};
  width: fit-content;
  text-align: center;
  margin: ${(props) => props.theme.spacing.x1_5}px auto 0;
  font: bold 15px/15px ${(props) => props.theme.fontFamily.secondaryFont};
  letter-spacing: -0.0333em;
  text-transform: uppercase;

  @media ${isPdf} {
    font-size: 20px;
    line-height: 20px;
    letter-spacing: -0.025em;
  }

  ${({ theme }) =>
    theme?.pdfMode &&
    css`
      @media ${device.pdf} {
        /* for some reason the padding is not even when generated as a pdf by BrightSites
          so we compensate with magic numbers until it looks even
        */
        padding-top: ${(props) => props.theme.spacing.x0_5 + 2}px;
        padding-bottom: ${(props) => props.theme.spacing.x0_5 - 2}px;
      }
    `}
`;

const TextSplashContent = styled((props) => {
  const { className, data, isSport, headShotData = {} } = props;
  const {
    body,
    author,
    authorLocation,
    capsule,
    headline,
    subheadline,
    articleRef,
    headlineSize = 'normal',
    subheadlineSize = 'normal',
  } = data;
  const { headshots = [] } = headShotData;
  const wobTitle = headshots.at(1)?.title;

  return (
    <Wrapper
      className={className}
      href={articleRef ? `../${articleRef.id}/index.html` : ''}
    >
      <HeadlineWrapper>
        {wobTitle && <WobTitle>{wobTitle}</WobTitle>}
        <Headline
          className={
            isSport
              ? `headline sport size-${headlineSize}`
              : `headline size-${headlineSize}`
          }
        >
          {headline}
        </Headline>
        {subheadline && (
          <SubHeadline
            className={`size-${subheadlineSize}`}
            dangerouslySetInnerHTML={{ __html: subheadline }}
          />
        )}
      </HeadlineWrapper>
      {body && (
        <ContentBody
          className={`body ${body.length > 300 ? 'col-balance' : 'col-fill'}`}
        >
          {author && (
            <Author className={isSport ? 'author sport' : 'author'}>
              {capsule && <Capsule>{capsule}</Capsule>}
              {author} <span className="author-info">{authorLocation}</span>
            </Author>
          )}
          <Markup dangerouslySetInnerHTML={{ __html: body }} />
        </ContentBody>
      )}
    </Wrapper>
  );
})``;

export default TextSplashContent;
